<template>
    <!-- 配送异常 -->
    <div class="heigth-div scrollBar" style="{height:wrapHeight+'px'}">
        <!-- <div class="point-div" v-for="(item,index) in items" :key="index">
            <span class="spanI">{{item.title}}</span>
            <span class="spanII">{{item.receivetime}}</span>
        </div> -->
        <div class="point-div">
            <span class="spanI">185号车卸料超时两小时</span>
            <span class="spanII">2018-3-27 9:30</span>
        </div>
        <div class="point-div">
            <span class="spanI">175号车回站超时两小时</span>
            <span class="spanII">2018-3-27 14:30</span>
        </div>
        <div class="point-div">
            <span class="spanI">162号运输超时两小时</span>
            <span class="spanII">2018-3-27 16:30</span>
        </div>
    </div>

</template>

<script>
    // import ZLCommonTable from '../../../../components/ZLCommonTable'
    export default {
        // components:{
        //     ZLCommonTable,
        // },
        props:['wrapHeight'],
        data() {
            return {
                items:[]
            }
        },
        watch: {
            "$store.state.noticnum":"getlist"
        },
        mounted() {
            //this.init();
        },
        methods: {
            init() {
                //this.initEchart();
                //this.setEchart();
                // this.getlist();
            },
            getlist(){
                const _this = this;
                this.fetch('/message/inbox/allByPage',{inbox:{status:0,type:"xx"}})
                .then(result => {
                    this.items = result.data.list
                    //this.$store.state.noticnum = result.data.total;
                })
            },
        }
    }
</script>

<style lang="less" scoped type="text/less">
    .point-div{
        margin-top: 10px;
        margin-left: 10px;
        display: flex;
        justify-content: space-between;
        .spanII{
            margin-right: 10px;
        }
    }
    .heigth-div{
        height: 95%;
        overflow-y:auto
    }
</style>



